// Admin notification styles.
$color_white: #ffffff;

#wpforms-notifications {

	position: relative;
	background: $color_white 0 0 no-repeat padding-box;
	box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
	border-radius: 6px;
	opacity: 1;
	min-height: 48px;
	margin: 0 0 20px 0;

	* {
		box-sizing: border-box;
	}

	.wpforms-notifications-header {
		display: flex;
		align-items: center;
		padding: 10px 16px;
		border-bottom: 1px solid rgba(204, 208, 212, 0.5);

		.wpforms-notifications-bell {
			position: relative;
			width: 16px;
			height: 20px;
			top: 3px;
			margin-right: 10px;
		}

		.wpforms-notifications-circle {
			position: absolute;
			width: 11px;
			height: 11px;
			border-radius: 50%;
			top: -4px;
			right: -1px;
			border: 2px solid $color_white;
		}

		.wpforms-notifications-title {
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 17px;
			color: #23282d;
		}
	}

	.wpforms-notifications-body {
		position: relative;
	}

	.wpforms-notifications-messages {
		padding: 16px 100px 16px 16px;

		.wpforms-notifications {

			&-message {
				display: none;

				&.current {
					display: block;
				}
			}

			&-title {
				color: #444444;
				font-size: 17px;
				font-weight: bold;
				line-height: 17px;
				margin: 0 0 -2px 0;
				min-height: 19px;
			}

			&-content {
				font-weight: normal;
				font-size: 14px;
				line-height: 18px;
				margin: 8px 0 41px 0;
				color: #777777;
			}

			&-buttons {
				margin: -30px 80px 0 0;

				a {
					margin: 0 10px 0 0;
					min-height: unset;
				}
			}
		}
	}

	.wpforms-notifications-badge {
		background-color: #f6f7f7;
		border-radius: 3px;
		color: #2c3338;
		cursor: pointer;
		font-size: 11px;
		margin-left: 10px;
		padding: 6px 8px;
		position: relative;
		text-decoration: none;
		text-transform: uppercase;
		top: -1px;
		white-space: nowrap;

		&:focus, &:hover {
			background-color: #f0f0f1;
			box-shadow: none;
			color: initial;
		}

		.fa {
			color: #d63638;
			font-size: 17px;
			padding-right: 6px;
			position: relative;
			top: 2px;
		}
	}

	.dismiss {
		position: absolute;
		top: 15px;
		right: 16px;
		width: 16px;
		height: 16px;
		color: #a7aaad;
		font-size: 16px;
		cursor: pointer;
		text-align: center;
		vertical-align: middle;
		line-height: 16px;

		&:hover {
			color: #dc3232;
		}
	}

	.navigation {
		position: absolute;
		bottom: 20px;
		right: 16px;
		width: 63px;
		height: 30px;

		a {
			display: block;
			width: 30px;
			height: 30px;
			border: 1px solid #7e8993;
			border-radius: 3px;
			font-size: 16px;
			line-height: 1.625;
			text-align: center;
			cursor: pointer;
			background-color: $color_white;
			color: #41454a;

			&:hover {
				background-color: #f1f1f1;
			}
		}

		.prev {
			float: left;
		}

		.next {
			float: right;
		}

		.disabled {
			border-color: #dddddd;
			color: #a0a5aa;
			cursor: default;

			&:hover {
				background-color: $color_white;
			}
		}
	}
}

.lity-iframe .lity-content {
	margin: 0 auto;
}

@media screen and (max-width: 768px) {

	#wpforms-notifications {

		.wpforms-notifications-messages {
			padding: 15px 50px 20px 16px;

			.wpforms-notifications-message {

				.wpforms-notifications-title {
					line-height: 22px;
					margin: 0 30px -2px 0;
					min-height: 24px;
				}

				.wpforms-notifications-content {
					font-size: 16px;
					line-height: 22px;
				}

				.wpforms-notifications-buttons {
					margin: -30px 80px 0 0;

					a {
						margin: 0;
						display: table;
					}

					.button-secondary {
						margin-top: 6px;
					}
				}
			}
		}
	}
}
